@import "../style.scss";
.Footer {
  width: 98%;
  min-height: 300px;
  background-color: #432564;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  @include Flex(column, initial, initial, initial, wrap);
  .top {
    @include Flex(row, space-around, initial, initial, wrap);
    .box-help {
      width: 190px;
      padding-left: 10px;
      margin-left: 5px;
      height: auto;
      background-color: #d9d3e0;
      border-top: 10px solid #432564;
      transform: translateY(-10px);
      padding-bottom: 20px;
      padding-top: 30px;
      img {
        width: auto;
        height: 30px;
      }
      p {
        @include Font(Roboto, 14px, 500, #432564, initial);
        margin-top: 20px;
      }
      a {
        @include Font(Roboto, 12px, 400, #432564, initial);
        line-height: 16px;
        background-repeat: no-repeat;
        background-size: 16px 16px;
        background-position: left center;
        text-indent: 20px;
        display: block;
        margin-top: 10px;
      }
      .tel {
        background-image: url("../../../public/assets/phone_icon1.svg");
      }
      .mail {
        background-image: url("../../../public/assets/mail_icon.svg");
      }
    }

    .box {
      min-width: 150px;
      margin-top: 30px;
      margin-left: 10px;
      @include Flex(column, initial, initial, initial, initial);
      @include Font(Roboto, 17px, 500, #ffffff, initial);
      h4 {
        color: #ffffff;
        u {
        }
      }
      .link {
        @include Font(Roboto, 15px, 400, #ffffff, initial);
        color: #ffffff;
        background-image: url("../../../public/assets/arrow.svg");
        background-repeat: no-repeat;
        background-size: 10px 10px;
        background-position: left center;
        text-indent: 20px;
        margin-top: 5px;
      }
    }

    .contact {
      min-width: 150px;
      margin-top: 40px;
      margin-left: 10px;
      @include Flex(column, initial, center, initial, initial);
      .link {
        width: 120px;
        height: 35px;
        line-height: 35px;
        display: block;
        border: 1px solid #ffffff;
        border-radius: 8px;
        @include Font(Roboto, 16px, 500, #ffffff, center);
        letter-spacing: 0.005em;
      }
      div {
        width: 100%;
        margin-top: 20px;
        @include Flex(row, space-around, initial, initial, initial);
        a {
          width: 20px;
          height: 20px;
          background-repeat: no-repeat;
          background-size: 15px 15px;
          background-position: center;
        }
        .fac {
          background-image: url("../../../public/assets/footerfacebook_icon.svg");
        }
        .lin {
          background-image: url("../../../public/assets/linkedin_icon.svg");
        }
        .ins {
          background-image: url("../../../public/assets/footerinstagram_icon.svg");
        }
      }
    }
  }

  .bottom {
    width: 95%;
    border-top: 1px solid #ffffff;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    span {
      @include Font(Roboto, 14px, 500, #ffffff, initial);
      word-spacing: 5px;
    }
    .tap-top {
      width: 45px;
      height: 45px;
      position: fixed;
      bottom: 50px;
      right: 50px;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-size: 40px 40px;
      background-position: center;
      background-image: url("../../../public/assets/top_icon.svg");
      border: 1px solid #432564;
      border-radius: 50%;
    }
  }
}
