@mixin Flex($fd, $jc, $ai, $ac, $fw) {
    display: flex;

    flex: {
        direction: $fd;
        wrap: $fw;
    }

    align: {
        items: $ai;
        content: $ac;
    }

    justify-content: $jc;
}

@mixin Font($ff, $fs, $fw, $fc, $ta) {
    font: {
        // family: $ff;
        size: $fs;
        weight: $fw;
    }

    color: $fc;
    text-align: $ta;
}

@mixin Animation($an, $ade, $adu, $adi, $afm, $aps) {
    animation-name: $an;
    animation-delay: $ade;
    animation-duration: $adu;
    animation-direction: $adi;
    animation-fill-mode: $afm;
    animation-play-state: $aps;
}